import React, { createContext, useEffect, useState } from 'react'
import { useCoinList } from '../hooks/useCoinList'
import { useWeb3 } from '../hooks/useWeb3'
import { getStorage, setStorage } from '../services/localStorage'
import { apiUrl } from '../services/apiService'
import Web3 from 'web3'
import req from '../services/req'
import red from '../services/selectorDeRed'
import validateReq from '../services/errorManager/validateError'
import errorManager from '../services/errorManager/errorManager'
const web3 = new Web3(window.ethereum)

export const DataContext = createContext()
export const DataProvider = ({ children }) => {

    

    //const networks = [1,56,137]
    const { coinList, setPrices } = useCoinList()
    const { getWallet, getChainId } = useWeb3()

    const ownerAddress = "0x37fDEd159F8bb80B69f29C18FC293c939F9cC5A3"

    const [loading, setLoading] = useState(false)
    const [loadingText,setLoadingText] = useState(false)
    const [validForm, setValid] = useState(false)
    const [wallet, setWallet] = useState(false)
    const [user, setUser] = useState(false)
    const [config, setConfig] = useState(false)
    const [darkMode, setDarkMode] = useState(true)
    const [comision, setComision] = useState(false)
    const [banks, setBanks] = useState(false)
    const [addBanks, setAddBanks] = useState(false)
    const [name, setName] = useState(1)
    const [money, setMoney] = useState(1)
    const [type, setType] = useState(1)
    const [number, setNumber] = useState(false)
    const [titular, setTitular] = useState(false)
    const [banksAdmin, setBanksAdmin] = useState([])
    const [redesModal, setRedesModal] = useState(false)
    const [chain, setChain] = useState(false)
    const [usdt, setUsdt] = useState(false)
    const [btcPrice, setBtcPrice] = useState(false)
    const [userLevel, setUserLevel] = useState(0)
    const [porcentajeCompraBTC, setPorcentageCompraBTC] = useState(false)
    const [porcentajeVentaBTC,setPorcentageVentaBTC] = useState(false)
    const [alertModal, setAlertModal] = useState({status: false, text: '', type: "success",link:false})
    const [usdPrice,setUsdtPrice] = useState(false)
    const [kyc,setKyc] = useState(false)
    const [modalCrypto, setModalCrypto] = useState(false)

    useEffect(() => {
        connect()
    }, [])

    const getPricesOfCryptoInBinance = async () => {
        try {
            let res = await fetch("https://api.binance.com/api/v3/ticker/price?symbol=BTCUSDT")
            let btc = await res.json()
            /* let btc = {price:20000} */
            setBtcPrice(btc)
            return btc.price
        } catch (error) {
            setBtcPrice(0)
            errorManager(error)
            console.log("Error en la api de binance - [getPricesOfCryptoInBinance]")
        }
    }

    const getTokens = async (wallet) => {
        let id = await getChainId()
        const usdtBalance = await red(id).usdtContract.methods.balanceOf(wallet).call()
        setUsdt(Number(web3.utils.fromWei(usdtBalance, "mwei")).toFixed(2))
    }

    const getLocalStorage = () => {
        let local = getStorage('bitmarketperu') || null
        if (!local) {
            setStorage('bitmarketperu', 'light')
            setDarkMode(false)
        } else local === 'light' ? setDarkMode(false) : setDarkMode(true)
    }

    const getBanks = async (wallet) => {
        const bankList = await req.get(apiUrl + "bank/" + wallet)
        validateReq(bankList, "getBanks 1")
        let banks = bankList.response.reverse()
        setBanks(banks)
        return banks
    }

    const deleteBank = async (_id, wallet) => {
        setLoading(true)
        setLoadingText("Eliminando Banco")
        let _res = await req.put(apiUrl + "bank/"+ wallet +"/" + _id)
        console.log(_res)
        validateReq(_res,"[deleteBank]")
        await getBanks(wallet)
        setLoading(false);setLoadingText(false);
    }

    const sendForm = async (e) => {
        setLoading(true)
        setLoadingText("Enviando Datos")
        setAddBanks(false)
        setModalCrypto(true)
        e.preventDefault()
        if (name === 1 || money === 1 || type === 1) {
            if (name === 1) alert("Debe elegir un banco")
            if (money === 1) alert("Debe elegir un tipo de moneda")
            if (type === 1) alert("Debe elegir un tipo de cuenta")
        } else {
            setAddBanks(false)
            let body = {
                wallet,
                name,
                titular,
                number,
                type,
                money
            }
            const res = await req.post(apiUrl + "bank", body)
            await getBanks(wallet)
            setLoading(false);setLoadingText(false);
        }
    }

    const connectChain = async (_chainId) => {
        setRedesModal(false)
        const chainId = web3.utils.toHex(_chainId)
        window.ethereum.request({ method: 'wallet_switchEthereumChain', params: [{ chainId }] })
            .then(res => {
                console.log(res)
                setChain(_chainId)
                connect()
            }).catch((error) => {
                console.log("Error al cambiar de red: ", error)
            })
    }

    const _getChainId = async () => {
        const id = await getChainId()
        setChain(id)
        return id
    }

    const connect = async () => {
        setLoading(true)
        setLoadingText("Cargando Datos, Por favor espere")
        getLocalStorage()
        let btc = await getPricesOfCryptoInBinance()
        await setPricesSoles(btc)
        
        if (window.ethereum){
            const wallet = await getWallet()
            getTokens(wallet)
            _getChainId()
            if (wallet) {
                setWallet(wallet)
                const body = { wallet }
                const auth = await req.post(apiUrl + "auth", body)
                const storage = await getStorage('loginToken') || null
               
                if(!storage) setStorage('loginToken', auth.response.token)

                //verificando token
                const verify = await ( await fetch(apiUrl + "auth/verify/" + storage)).json()
                if(verify.error) setStorage('loginToken', auth.response.token)
               
                setUserLevel(auth.response.level)/* 
                console.log('connect 2', auth.response.level, userLevel) */
                setBanksAdmin(auth.response.banksAdmin)

                // ;) el bendito error aqui
                if(!verify?.error) await getBanks(wallet)

                setUser(auth.response)
                const config = auth.response.config[0]
                setConfig(config)
                setKyc(auth.response.kyc)
                console.log(auth.response)
                setComision(config.solOut)
                setPorcentageCompraBTC(config.buyBit)
                setPorcentageVentaBTC(config.sellBit)
                setLoading(false);setLoadingText(false);
                setLoadingText(false)
            } else {
                setLoading(false);setLoadingText(false);
                setLoadingText(false)
            }
        } else {
            setAlertModal({status: true, text: 'Por favor instale Metamask', type: "warnign",link:"https://blog.bitmarketperu.com/que-es-metamask/"})
            console.log("Por favor instale Metamask")
            setLoading(false);setLoadingText(false);
        }
        if(!window.ethereum._state.isUnlocked) setAlertModal({status: true, text: 'Por favor conecte su billetera Metamask', type: "warning",link:false})
    }

    const setPricesSoles = async (btc) => {
        let precioDolar = await fetch(apiUrl + "proxy")
        let Proxy = await precioDolar.json()
        let resProxy = Proxy.response
        let precioCompra = 1/Number(resProxy[0].com_divisa)
        let precioVenta = 1/Number(resProxy[0].ven_divisa)
        setUsdtPrice({
            compra:resProxy[0].com_divisa,
            venta:resProxy[0].ven_divisa
        })
        setPrices(precioCompra, precioVenta,btc)
    }

    const context = {
        connect, wallet,
        loading, setLoading,loadingText,setLoadingText,
        user, config,
        darkMode, setDarkMode,
        comision, setComision,
        coinList,kyc,
        usdt, setUsdt,
        deleteBank, sendForm, addBanks,
        setAddBanks, setName, setMoney,
        setType, setNumber, setTitular,modalCrypto, setModalCrypto,
        validForm, banks, setValid, banksAdmin,
        alertModal, setAlertModal,getBanks,
        setRedesModal, redesModal, connectChain, chain, getChainId, ownerAddress, btcPrice, userLevel
        , validateReq,porcentajeCompraBTC, porcentajeVentaBTC,usdPrice,_getChainId
    }

    return (
        <DataContext.Provider value={context}>
            {children}
        </DataContext.Provider>
    )
}


