import styles from '../../css/swap/swap.module.css'
import { Grafic } from '../../components/grafic/grafic'
import { useContext } from 'react'
import { DataContext } from '../../context/DataContext'
import Loader from '../../components/loader/Loader'
import InvertSwap from '../../components/swap/invertSwap'
import Modal from '../../components/modals/modal'
import { useSwap } from '../../hooks/useSwap'
import TransactionModal from '../../components/modals/transactionModal'
import AddBankModal from '../../components/modals/addBankModal'
import LogoBank from '../../components/logoBank/logoBank'
import AlertModal from '../../components/modals/alertModal'
import Web3 from 'web3'
import logo from '../../img/logoAzul.png'
import DarkMode from '../../components/darkMode/DarkMode'
import { getStorage, setStorage } from '../../services/localStorage'
import bsc from '../../img/redes/bsc.png'
import matic from '../../img/redes/matic.png'
import eth from '../../img/redes/eth.svg'
import Resume from '../../services/resumeWallet'
import ModalPayType from '../../components/modals/modalPayType'
import ModalBtc from '../../components/modals/modalBtc'
const web3 = new Web3(window.ethereum)
function Swap() {

    const { setLoading, comision, loading, usdt, alertModal, setAlertModal,
        addBanks, setAddBanks, banks, wallet, banksAdmin, chain, btcPrice,
        usdPrice, _getChainId, darkMode, setDarkMode, setRedesModal, loadingText, setLoadingText } = useContext(DataContext)

    const { modalActive, setModalActive, selected, selected2,
        filter, input1, input2, sendTransaction, openModal,
        executeSelection, invertSwap, calc, error, grafic,
        transactionModal, dataStyle, setTransactionModal, modalCrypto,
        setModalCrypto, selectBankItem, setInput1, setInput2, modalBtc,
        setModalBtc, modalPayType, setModalPayType, bank, sendMetamask, sendTransactionBinancePay
    } = useSwap()

    const machineCrypto = (usdt) => {
        let aux = Math.floor(usdt).toString()
        aux = web3.utils.fromWei(aux, "microether")
        aux = Number(aux).toFixed(2)
        return aux
    }

    const fixed2 = (price) => {
        return Number(price).toFixed(2)
    }

    const handleDarlk = () => {
        let local = getStorage('bitmarketperu')
        if (local === 'light') {
            setStorage('bitmarketperu', 'dark')
            setDarkMode(true)
        } else {
            setStorage('bitmarketperu', 'light')
            setDarkMode(false)
        }
    }


    function soloNumeros(e){
        
        let key = e.keyCode || e.which;
        
        let tecla = String.fromCharCode(key).toLowerCase();
        
        let letras = "1234567890.";
        let especiales = "";

        let tecla_especial = false
        
        for(var i in especiales){
            if(key === especiales[i]){
                tecla_especial = true;
        alert(tecla);
                break;
            }
        }

        if(letras.indexOf(tecla) === -1 && !tecla_especial){
            return false;
        }
   };

    return (<div className='swapContainer'>
        <div className='d-lg-none nav-section-1 nav-mobile'>
            <div className='logo-mobile'>
                <img src={logo} alt="" />
                <span className={`${!darkMode && "nav-text-logo"}`}>BitmarketPerú</span>
            </div>
            <DarkMode setDarkMode={() => handleDarlk()} darkMode={darkMode} />
            <div className='nav-section-2'>
                <button onClick={() => setRedesModal(true)} className='connect-btn'>
                    <div className='external-circle'>
                        {chain && chain == 1 && <div className='internal-circle'>
                            <img className='redLogoWallet' src={eth} alt="" />
                        </div>}
                        {chain && chain == 56 && <div className='internal-circle'>
                            <img className='redLogoWallet' src={bsc} alt="" />
                        </div>}
                        {chain && chain == 137 && <div className='internal-circle'>
                            <img className='redLogoWallet' src={matic} alt="" />
                        </div>}
                        {!chain && <>
                            <div className='internal-circle'>
                                <i className='bi-wallet2 text-white' ></i>
                            </div>
                        </>}
                    </div>
                    <div style={{ fontSize: '16px' }}>
                        {wallet ? Resume(wallet) : <>Conectar Wallet</>}
                    </div>
                </button>
            </div>
        </div>
        <div style={dataStyle} className="x">

            {modalBtc && <ModalBtc setInput1={setInput1} setInput2={setInput2} input1={input1} input2={input2} setModalBtc={setModalBtc} i1={input1} selected={selected} selected2={selected2} setAlertModal={setAlertModal} />}

            {alertModal.status && <AlertModal setAlertModal={setAlertModal} alertModal={alertModal} />}

            {loading && <Loader text={loadingText} />}

            {modalPayType && <ModalPayType sendTransactionBinancePay={sendTransactionBinancePay} input1={input1} input2={input2} selected2={selected2} selected={selected} sendMetamask={sendMetamask} bank={bank} setModalPayType={setModalPayType} />}

            {modalBtc && <ModalBtc setInput1={setInput1} setInput2={setInput2} input1={input1} input2={input2} setModalBtc={setModalBtc} i1={input1} selected={selected} selected2={selected2} setAlertModal={setAlertModal} />}

            {alertModal.status && <AlertModal setAlertModal={setAlertModal} alertModal={alertModal} />}

            {transactionModal && <TransactionModal setLoadingText={setLoadingText} _getChainId={_getChainId} setAlertModal={setAlertModal} setInput1={setInput1} setInput2={setInput2} setLoading={setLoading} banks={banks} input1={input1} input2={input2} banksAdmin={banksAdmin} wallet={wallet} setTransactionModal={setTransactionModal} selected={selected} selected2={selected2} />}

            {modalActive && <Modal executeSelection={executeSelection} filter={filter} setModalActive={setModalActive} />}

            {addBanks && <AddBankModal />}

            {modalCrypto &&
                <div className="modal-wrapper">
                    <div className="modal-display">
                        <div className='d-flex justify-content-betwen p-2 align-items-center'>
                            <div className='coinChanges'>
                                {selected.name} <i className="bi bi-arrow-right"></i> {selected2.name}
                            </div>
                            <i onClick={() => setModalCrypto(false)} className="bi-x x" />
                        </div>

                        <div className="modalContent">
                            {selected2 && selected2.type == "solesEfect" | selected2.type == "dolaresEfect" ? <div className='p-4'>
                                <div className='b-tittle'>
                                    Retirar {selected2.name} por taquilla <br />
                                </div>
                                <b className='b-result'> Dirección: Lima, miraflores, sector xxx callet xxx local xxx </b><br />
                                
                                <div className='mb-4'>
                                    <a className='a-link' target="_blank" href="https://www.google.com/maps/place/CASA+DE+CAMBIO+MIRAFLORES+EWFOREX.NET+%7C+MONEY+EXCHANGE/@-12.1148124,-77.0472571,18z/data=!4m5!3m4!1s0x9105c8372ba2dfb9:0xfb63bcfde6c7a8b6!8m2!3d-12.1140702!4d-77.0475521?shorturl=1">
                                        <i className='bi bi-pin-map' /> Ir a ubicación en Google Map
                                    </a>
                                </div>
                                <button className='form-control' onClick={() => { selectBankItem("000000000000000000000000"); setModalCrypto(false) }} > Continuar </button>
                            </div> : <>
                                <span className="p-3 b-result">Elija el banco donde recibes
                                    <i className='px-1 text-white'>
                                        {selected2.name}
                                    </i>
                                </span>
                                <button className='addBank mx-4' onClick={() => { setModalCrypto(false); setAddBanks(true) }} > <i className='bi-plus-circle' /> Agregar Banco</button>
                                <div className="bankList">
                                    {banks && banks.map((item) => item.money.toLowerCase() == selected2.category && <div key={item._id}>
                                        <div onClick={() => {
                                            selectBankItem(item);
                                            setModalCrypto(false)
                                        }} key={item._id} className="bankItem">
                                            <LogoBank name={item.name} />
                                            <div>
                                                {item.name} - {item.money}<br />
                                                <p className='secondaryText'>
                                                    {item.titular} <br />
                                                    {item.number}
                                                </p>
                                            </div>
                                        </div>
                                    </div>)}
                                </div>
                            </>}
                        </div>
                    </div>
                </div>
            }

            <div style={grafic} className='container'>
                <div className="row">
                    <div className="col-7 pad text-white d-none d-md-none d-lg-block">
                        <div className={styles.grafic}>
                            <Grafic selected={selected} selected2={selected2} />
                        </div>
                    </div>
                    <div className="col-md-12 col-lg-5 pad mb-5">
                        <div className={styles.swapSection}>
                            <h2> Swap </h2>
                            <p> Cambia tokens al instante </p>
                            <span className='lb'>
                                Precio BTC: {btcPrice && fixed2(btcPrice.price)} USD <br />
                                Compra USD: {usdPrice && usdPrice.compra}s - Venta USD:{usdPrice && usdPrice.venta}s
                            </span>
                            <hr />
                            <div className='selectSwapItem mb-2'>
                                <div onClick={() => openModal(1)} className={styles.select}>
                                    {selected && <img src={selected.img} alt="" />}
                                    <div className='mx-2'>{selected && selected.name}</div>
                                    <i className="bi-chevron-down"></i>
                                </div>
                                <div className='lb'>
                                    {selected && selected.name == "USDT" && <>
                                        Balance {usdt ? <> {chain == 56 ? machineCrypto(usdt) : usdt} USDT </> : "Loading..."}
                                    </>}
                                </div>
                            </div>
                            <input value={input1} onChange={(e) => { calc(e.target.value, 1) }} type="text" placeholder='0.0' />

                            <InvertSwap invertSwap={invertSwap} />

                            <div className='mb-2 selectSwapItem'>
                                <div onClick={() => openModal(2)} className={styles.select}>
                                    {selected2 && <img src={selected2.img} alt="" />}
                                    <div className='mx-2'>{selected2 && selected2.name}</div>
                                    <i className="bi-chevron-down"></i>
                                </div>
                                <div className='lb'>
                                    {selected2 && selected2.name == "USDT" && <>

                                        Balance {usdt ? <> {chain == 56 ? machineCrypto(usdt) : usdt} USDT </> : "Loading..."}
                                    </>}
                                </div>
                            </div>
                            <input value={input2} onChange={(e) => { calc(e.target.value, 2) }} type="text" placeholder='0.0' name="2" id="2" />
                            <div className='text-danger'>
                                {error && error}
                            </div>
                            <div className='mb-2 lb'>
                                Comision {comision}%
                            </div>
                            <div>
                                {!error && input1 ?
                                    <button onClick={() => sendTransaction(selected)} > Procesar </button> :
                                    <button className='btnDisabled' > Procesar </button>
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>)
}
export default Swap