//type = success || warning

const AlertModal = ({ alertModal, setAlertModal }) => {

    const handleDefault = () => null;

    const handle = alertModal?.handleBTN || handleDefault;

    const typeIcon = (alertModal.type === "success") ? <i
        style={{ color: "rgb(40, 156, 66)" }}
        className="bi bi-check-circle"></i>
        : <i
            style={{ color: "rgb(223, 203, 55)" }}
            className="bi bi-exclamation-circle-fill"></i>

    return (<div className="modal-wrapper">
        <div className="modal-display p-4">
            <div className="centerAlertModal">
                {typeIcon}
                <p>
                    {alertModal.text}
                </p>
        
                <button onClick={() => {setAlertModal({ status: false, text: "", type: "success" });handle()}}>
                    Ok
                </button>
                
                <div className="pt-4 linkMetamask">
                {alertModal.link && <a target="_blank" href={alertModal.link} > ¿Como usar metamask?, Click aqui </a>}
                </div>
            </div>
        </div>
    </div>)
}
export default AlertModal