import dobleArrow from '../../img/dobleArrow.svg'
const InvertSwap = ({invertSwap})=>{
    return (<>
        <div className="dobleArrowWrap">
            <div onClick={invertSwap}  className="dobleArrowCircle">
                <img src={dobleArrow} alt="" />
            </div>
        </div>
    </>)
}
export default InvertSwap