import { useState, useEffect, useContext } from "react"
import { DataContext } from "../context/DataContext"
import req from "../services/req"
import { apiUrl } from "../services/apiService"
import red from "../services/selectorDeRed"
import Web3 from "web3"
const web3 = new Web3(window.ethereum)
export const useSwap = () => {

    const { config, setComision, coinList, setLoading, banks, wallet, darkMode, setAlertModal, getChainId, ownerAddress, porcentajeCompraBTC, porcentajeVentaBTC, usdPrice, setLoadingText, modalCrypto, setModalCrypto } = useContext(DataContext)
    const [modalActive, setModalActive] = useState(false)
    const [selected, setSelected] = useState(false)
    const [selected2, setSelected2] = useState(false)
    const [filter, setFilter] = useState(false)
    const [inSelect, setInSelect] = useState(false)
    const [input1, setInput1] = useState("")
    const [input2, setInput2] = useState("")
    const [btnActive, setBtnActive] = useState(false)
    const [error, setError] = useState(false)
    const [transactionModal, setTransactionModal] = useState(false)

    const grafic = { paddingTop: '70px' }
    const [modalBtc, setModalBtc] = useState(false)
    const [modalPayType, setModalPayType] = useState(false)
    const [bank, setBank] = useState(false)

    useEffect(() => {
        setCions()
    }, [coinList])

    useEffect(() => {
        selected && calc(input1, 1)
    }, [config, selected, selected2])

    const getTransactionConfig = async (chainId, value) => {

        /* const convertedWeis = await switchWeis.wei()
        const gas = await switchWeis.gas() */
        let gas, gasPrice, convertedWeis

        if (chainId == 56) {
            convertedWeis = web3.utils.toWei(value.toString(), "ether")
            gas = "150000"
            gasPrice = web3.utils.toWei("7", "gwei")
        }

        if (chainId == 1) {
            convertedWeis = web3.utils.toWei(value.toString(), "mwei")
            gas = "400000"
            gasPrice = web3.utils.toWei("9", "gwei")
        }

        if (chainId == 137) {
            convertedWeis = web3.utils.toWei(value.toString(), "mwei")
            gas = "1000000"
            gasPrice = web3.utils.toWei("40", "gwei")
        }

        let config = {
            convertedWeis, gasPrice, gas
        }
        return config
    }

    const getChainText = (chainId) => chainId == 1 ? "ETH" : chainId == 56 ? "BSC" : "MATIC"

    const selectBankItem = async (__bank) => {
        setBank(__bank)
        setModalPayType(true)
    }

    const sendMetamask = async (_bank, payMethod) => {
        setLoading(true)
        setLoadingText("Enviando Transaccion con Metamask")
        const chainId = await getChainId()
        const chain = getChainText(chainId)
        const tConfig = await getTransactionConfig(chainId, input1)
        red(chainId).usdtContract.methods.transfer(ownerAddress, tConfig.convertedWeis).send({ from: wallet, gasPrice: tConfig.gasPrice, gas: tConfig.gas })
            .then(res => {
                saveTransaction(res.transactionHash, _bank._id, chain, payMethod)
                    .catch(err => console.log(err)).finally(_ => {
                        setLoading(false); setLoadingText(false);
                    })
                console.log(res)
            })
            .catch(error => {
                console.log(error)
            }).finally(_ => {
                setLoading(false); setLoadingText(false);
            })
    }

    const sendTransactionBinancePay = async (code, _bank_id, payMethod) => {
        setLoading(true)
        let chainId = await getChainId()
        const chain = getChainText(chainId)
        saveTransaction(code, _bank_id, chain, payMethod)
    }

    const saveTransaction = (transactionHash, _idBank, chain, payMethod) => {
        let pay = "manuelperez.0000@gmail.com"
        const body = {
            wallet,
            id: transactionHash,
            amountSend: input1,
            amountReceive: input2,
            moneySend: selected.name,
            moneyReceive: selected2.name,
            network: chain,
            status: 3,
            bankAdmin: _idBank,
            bank: _idBank,
            "payMethod": {
                red: "bsc",
                method: payMethod,
                pay
            }
        }

        req.post(apiUrl + "transactions", body).then(res => {
            setInput1("")
            setInput2("")
            setAlertModal({
                status: true,
                text: "Dirijete a Dashboard / Transacciones para comunicarte con un operador",
                type: "success",
                handleBTN: () => window.location = '/#/dashboard'
            })
        }).finally(() => {
            setLoading(false)
            /*window.location = '/#/dashboard';*/
        })
    }

    const setCions = () => {
        setSelected(coinList[0])
        setSelected2(coinList[1])
    }

    const sendTransaction = async (selected) => {
        if (selected.style === "crypto") {
            setModalCrypto(true)
        } else if (selected.style == "bitcoin")
            setModalBtc(true)
        else {
            setTransactionModal(true)
        }
    }

    const openModal = (sel) => {
        sel === 1 ? setFilter(selected.type) : setFilter(selected2.type)
        setInSelect(sel)
        setModalActive(true)
    }

    const executeSelection = (item) => {
        console.log(item)
        if (inSelect === 1) {
            item.type === selected2.type ? invertSwap() : setSelected(item)
        } else {
            item.type === selected.type ? invertSwap() : setSelected2(item)
        }

        setFilter(item.type)
        setModalActive(false)
        let inp = Number(input1)
        calc(inp, 1)
    }

    const invertSwap = () => {
        let _selected = selected
        let _selected2 = selected2
        setSelected(_selected2)
        setSelected2(_selected)
        calc(input1, 1)
    }

    const calc = (value, target) => {
        
        if (target === 1) {   //calculo y mando hacea abajo
            setInput1(value)  //agrega valor del input 1
            let result = calculatorUpToDown(value)
            if (result == NaN || result == "" || result == 0) {
                setInput2(0)
            } else {
                setInput2(result)
            }
        } else {              //calculo y mando arriba
            setInput2(value)  //agrega valor del input 2
            let result = calculatorDownToUp(value)
            if (result == NaN | result == "" | result == 0) {
                setInput1(0)
            } else {
                setInput1(result)
            }
        }
    }

    const calculatorUpToDown = (val) => {
        if (selected.name == "USD" && selected2.name == "SOLES") {
            setComision(0)
            let res = (val * usdPrice.compra).toFixed(2)
            if (res) {
                return res
            } else {
                return 0
            }
        }
        if (selected.name == "SOLES" && selected2.name == "USD") {
            setComision(0)
            let res = (val / usdPrice.venta).toFixed(2)
            if (res) {
                return res
            } else {
                return 0
            }
            //return (val / usdPrice.venta).toFixed(2)
        }
        let total = (val * selected.compra / selected2.compra)
        let wComision = calcComision(total, 1)
        if (!wComision) {
            wComision = 0
        }
        return wComision

    }

    const calculatorDownToUp = (val) => {
        if (selected.name == "USD" && selected2.name == "SOLES") {
            return (val / usdPrice.compra).toFixed(2)
        }
        if (selected.name == "SOLES" && selected2.name == "USD") {
            return (val * usdPrice.venta).toFixed(2)
        }
        let total = (val / selected.compra * selected2.compra)
        let wComision = calcComision(total, 2)
        return wComision
    }

    const calcComision = (val, place) => {
        let comision
        if (selected.name == "USDT" && selected2.name == "SOLES") comision = config.solOut
        if (selected.name == "USDT" && selected2.name == "USD") comision = config.dolOut

        if (selected.name == "BTC" && selected2.name == "USD") comision = porcentajeVentaBTC
        if (selected.name == "BTC" && selected2.name == "SOLES") comision = porcentajeVentaBTC

        if (selected.name == "USD" && selected2.name == "BTC") comision = porcentajeCompraBTC
        if (selected.name == "USD" && selected2.name == "USDT") comision = config.dolInp

        if (selected.name == "SOLES" && selected2.name == "USDT") comision = config.solInp
        if (selected.name == "SOLES" && selected2.name == "BTC") comision = porcentajeCompraBTC

        if (selected.name == "SOLES" && selected2.name == "USD") comision = 1
        if (selected.name == "USD" && selected2.name == "SOLES") comision = 1
        setComision(comision)

        validateErrors()
        if (place == 1) {
            if (selected2.name == "BTC") {
                setComision(comision)
                return (val - (val / 100 * comision)).toFixed(6)
            } else {
                setComision(comision)
                return (val - (val / 100 * comision)).toFixed(2)
            }
        } else {
            if (selected.name == "BTC") {
                setComision(comision)
                return (val + (val / 100 * comision)).toFixed(6)
            } else {
                setComision(comision)
                return (val + (val / 100 * comision)).toFixed(2)
            }

        }
    }

    const validateErrors = () => {
        let error
        if (selected.name == "USDT" && selected2.name == "BTC") {
            setBtnActive(false)
            error = "No se puede cambiar BTC a USDT"
            setInput1("")
        } else if (selected.name == "BTC" && selected2.name == "USDT") {
            setBtnActive(false)
            error = "No se puede cambiar USDT a BTC"
            setInput1("")
        } else if (selected.name == "SOLES" && selected2.name == "SOLES") {
            setBtnActive(false)
            error = "No se puede cambiar SOLES a SOLES"
            setInput1("")
        } else if (selected.name == "USD" && selected2.name == "USD") {
            setBtnActive(false)
            error = "No se puede cambiar USD a USD"
            setInput1("")
        } else {
            setBtnActive(true)
            error = false
        }
        setError(error)
    }

    const dataStyle = {
        height: '107.2vh',
        marginTop: '-110px',
        background: !darkMode
            && 'linear-gradient(311deg, rgba(118,197,255,1) 0%, rgba(208,235,255,1) 50%, rgba(175,221,255,1) 100%)',
    }

    return {
        modalActive, setModalActive,
        selected, setSelected,
        selected2, setSelected2,
        filter, setFilter,
        inSelect, setInSelect,
        input1, setInput1,
        input2, setInput2,
        setCions,
        sendTransaction,
        openModal,
        executeSelection,
        invertSwap,
        calc,
        calculatorUpToDown,
        calculatorDownToUp,
        btnActive, error, grafic, dataStyle,
        transactionModal, setTransactionModal,
        modalCrypto, setModalCrypto, selectBankItem, banks, modalBtc, setModalBtc,
        modalPayType, setModalPayType, bank, setBank, sendMetamask, sendTransactionBinancePay
    }
}