import { useBoard } from '../../hooks/useBoard'
import Loader from '../../components/loader/Loader'
import { DataContext } from '../../context/DataContext'
import { useContext } from 'react'
const Perfil = () => {
    const { kyc, darkMode } = useContext(DataContext)
    const { setAddData, sendFormData, name, email, phone, user, addData, setName, setEmail, setPhone, loading } = useBoard()

    return (<>
        {loading && <Loader />}
        {addData &&
            <div className='bgtransparentDark'>
                <div className='modal-display'>
                    <div className='modalHeader'>
                        <div>
                            Agregar Datos Personales
                        </div>
                        <div>
                            <i onClick={() => setAddData(false)} className='bi-x x'></i>
                        </div>
                    </div>
                    <div className='backOptions'>
                        <form action="" onSubmit={(e) => sendFormData(e)}>
                            <span> <i className='bi-people' /> Nombre completo</span>
                            <input value={name} onChange={e => setName(e.target.value)} className='form-control mb-2' type="text" placeholder='Escriba su nombre' required />

                            <span> <i className='bi-envelope' /> Correo Electronico </span>
                            <input value={email} onChange={e => setEmail(e.target.value)} className='form-control mb-2' type="email" placeholder='Correo electronico' required />

                            <span> <i className='bi-telephone' /> Numero de telefono </span>
                            <input value={phone} onChange={e => setPhone(e.target.value)} className='form-control mb-2' type="text" placeholder='Numero de telefono' required />

                            <button className="saveBtb"> Guardar </button>
                        </form>
                    </div>
                </div>
            </div>
        }
        <div className="p-3">
            <div className="bancos">

                <h4 className={darkMode ? 'textDark' : "textLight"}>Datos personales</h4>
                <button onClick={() => setAddData(true)}> <i className='bi-plus-circle' /> {name ? "Editar" : "Agregar"} </button>
            </div>
            <div className='datos mt-3'>
                <div className="datosList">
                    <div>
                        <i className='bi-person mx-2' />
                        {user.name ? user.name : <> Nombre no asignado <i className='lb'> ( Opcional )</i></>}
                    </div>
                </div>
                <div className="datosList">
                    <div>
                        <i className='bi-envelope mx-2' />
                        {user.email ? user.email : <> Correo electronico no asignado <i className='lb'> ( Opcional )</i></>}
                    </div>
                </div>
                <div className="datosList">
                    <div>
                        <i className='bi-telephone mx-2' />
                        {user.phone ? user.phone : <>Telefono no asignado<i className='lb'> ( Opcional )</i></>}
                    </div>
                </div>
            </div>
        </div>
        <hr className='my-5' />

        <div className='p-3 mb-5'>
            <h4 className={darkMode ? 'textDark' : "textLight"}>KYC</h4>
            <div className={darkMode ? 'textDark pb-4' : "textLight pb-4"}>
                La verificacion del kyc se realizara via Correo electronico, deberas enviar los requisitos al siguiente correo<br />
                <h4 className='mt-3 '><i className='bi bi-envelope'></i> manuelperez.0000@gmail.com  </h4>
            </div>
            <div className='container-fluid'>

                <div className='row'>
                    <div className='col-12 col-sm-6 col-md-4'>
                        <div className='datosList kyc'>
                            <div className={kyc ? 'kycVerificationSuccess' : "kycVerification"}>
                                <b>Foto delatera del DNI</b>
                                <h1> <i className='bi bi-exclamation-triangle' /> </h1>
                                <p>{kyc ? 'Verificada' : "No verificada"}</p>
                            </div>
                        </div>
                    </div>
                    <div className='col-12 col-sm-6 col-md-4'>
                        <div className='datosList kyc'>
                            <div className={kyc ? 'kycVerificationSuccess' : "kycVerification"}>
                                <b>Foto trasera del DNI</b>
                                <h1> <i className='bi bi-bookmark-check' /> </h1>
                                <p>{kyc ? 'Verificada' : "No verificada"}</p>
                            </div>
                        </div>
                    </div>
                    <div className='col-12 col-sm-6 col-md-4'>
                        <div className='datosList kyc'>
                            <div className={kyc ? 'kycVerificationSuccess' : "kycVerification"}>
                                <b>Foto del rostro al lado del DNI</b>
                                <h1> <i className='bi bi-bookmark-check' /> </h1>
                                <p>{kyc ? 'Verificada' : "No verificada"}</p>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>)
}
export default Perfil