import { useContext, useState, useEffect } from "react"
import { DataContext } from "../../context/DataContext"
import AlertModal from "./../modals/alertModal"
import { useWeb3 } from "../../hooks/useWeb3"
const BinancePay = ({ sendTransaction, setModalBtc, i1, selected2, userBank, setUserBank, setPay }) => {
    const [banks, setBanks] = useState(false)
    const { getBanks } = useContext(DataContext)
    const [code, setCode] = useState(false)
    const pay = "manuelperez.0000@gmail.com"
    const [alertModal, setAlertModal] = useState({ status: false, text: '', type: "success" })
    const { getWallet } = useWeb3()
    useEffect(() => {
        _getBanks()
    }, [])

    const _getBanks = async () => {
        let wallet = await getWallet()
        let _banks = await getBanks(wallet)
        console.log(_banks)
        setBanks(_banks)
    }

    const send = () => {
        //setModalBtc(false)
        setPay(pay)
        if (userBank && code) {
            sendTransaction(code)
        } else {
            console.log("Debe completar todos los campos");
            setAlertModal({ status: true, text: "Debe completar todos los campos", type: "warning" });
        }
    }

    return (<>
        {alertModal.status && <AlertModal setAlertModal={setAlertModal} alertModal={alertModal} />}

        Debes enviar {i1} BTC al siguente correo de Binance Pay<br />
        <p> <i className="bi bi-envelope" /> Manuelperez.0000@gmail.com <i className="bi bi-file-earmark-medical"></i> </p>
        <hr />


        {selected2.type == "solesEfect" | selected2.type == "dolaresEfect" ? <>
            Recibe  {selected2.name} en efectivo {() => { setUserBank("000000000000000000000000") }}
        </> : <>
            Elija el banco donde desea recibir {selected2.name}

            {banks ? banks.lenght == 0 ? "Debe agregar un banco en su Dashboard" :
                <select className="form-control" onChange={e => setUserBank(e.target.value)} name="" id="">
                    <option value={false}>- Elija uno de sus bancos - </option>
                    {banks.map(item => {
                        return <option key={item._id} value={item._id}>{item.name} - {item.money}</option>
                    })}
                </select> : "Cargando tus bancos"
            }
        </>}
        <hr />
        <span> Ingrese en numero de orden o hash de la transaccion </span>
        <input onChange={e => setCode(e.target.value)} type="text" className="form-control" />

        <button onClick={send} > Enviar </button>
    </>)
}
export default BinancePay