import { useState, useContext } from "react"
import { DataContext } from "../../context/DataContext"
import BinancePay from "../btcModal/binancepay"
import ExternalWallet from "../btcModal/externalWallet"
import { useWeb3 } from "../../hooks/useWeb3"
import req from "../../services/req"
import { apiUrl } from "../../services/apiService"
const ModalBtc = ({ setModalBtc, i1, selected, selected2, input1, input2, setInput1, setInput2, setAlertModal }) => {

    const { getChainId,getWallet } = useWeb3()
    const { setLoading, setLoadingText } = useContext(DataContext)

    const [method, setMethod] = useState(false)
    const [red, setRed] = useState(false)
    const [userBank, setUserBank] = useState(false)
    const [pay, setPay] = useState("manuelperez.0000@gmail.com")

    const sendTransaction = async (code) => {
        let chain = await getChainId()
        let wallet = await getWallet()
        chain == 1 ? chain = "ETH" : chain == 56 ? chain = "BSC" : chain = "MATIC"
        let body = {
            wallet,
            "id": code,
            "bank": userBank,
            "bankAdmin": userBank,
            "amountSend": input1,
            "amountReceive": input2,
            "moneySend": selected.name,
            "moneyReceive": selected2.name,
            "network": chain,
            "status": 3,
            "payMethod": {
                red,
                method,
                pay
            }
        }
        console.log(body)
        setLoading(true, "Enviando transacción")
        setLoadingText("Enviando transaccion")
        req.post(apiUrl + "transactions", body).then(_ => {
            console.log(_.response)
            setModalBtc(false)
            setLoading(false); setLoadingText(false);
            setAlertModal({
                status: true,
                text: "Dirijete a Dashboard / Transacciones para comunicarte con un operador",
                type: "success",
                handleBTN: () => window.location = '/#/dashboard'
            })
            setInput1("")
            setInput2("")
        }).catch(e => {
            setAlertModal({ status: true, text: "Error en la peticion", type: "warning" })
            console.log(e)
        })
    }

    const methodRed = (_method) => {
        if (_method == 1) {
            setMethod(1)
            setRed("binance")
        } else if (_method == 2) {
            setRed("BTC")
            setPay("0x34567890123456789012345678901234567890")
            setMethod(2)
        }
    }

    return (<>
        <div className="modal-wrapper">
            <div className="modal-display">
                <div className="modalHeader">
                    <div>
                        {selected && <div className="coinChanges">
                            {selected.name} <i className="bi bi-arrow-right"></i> {selected2.name}
                        </div>}
                    </div>
                    <div>
                        <i onClick={() => setModalBtc(false)} className="bi-x x" />
                    </div>
                </div>

                <div className="modalContent p-4">
                    Envias: {i1} BTC <br />
                    Metodo por donde envias BTCz:
                    <select className="form-control" onChange={e => methodRed(e.target.value)} name="" id="">
                        <option value={false}> - Elija un metodo de envio - </option>
                        <option value="1">Binance Pay</option>
                        <option value="2">Wallet Externa</option>
                    </select>

                    {method == 1 ? <BinancePay setModalBtc={setModalBtc} setPay={setPay} userBank={userBank} setUserBank={setUserBank} sendTransaction={sendTransaction} selected2={selected2} i1={i1} /> :
                        method == 2 && <ExternalWallet setPay={setPay} sendTransaction={sendTransaction} userBank={userBank} red={red} setUserBank={setUserBank} i1={i1} selected2={selected2} setRed={setRed} />}

                </div>

                {/*
                    <select name="" id="">
                        <option value={false}> - Elija su banco - </option>
                        {bank && bank.map((item)=>{
                            return (<option> {item.name } { console.log(item)} </option>)
                        })}
                    </select>

                </>}
                {method == 1 ? <>
                Ingresa el id de operacion:
                    <input type="text" className="form-control mb-3" />
                    <button> Procesar </button> </>: method == 2 && red != false && <button> Procesar </button>
                
                }
            </div> */}

            </div>
        </div>
    </>)
}
export default ModalBtc