import { useState } from "react"
import CoinList from '../services/coinList'
export const useCoinList = () => {

    const [coinList, setCoinList] = useState(false)
    const setPrices = (compra, venta, btc) => {
        let _coinList = []
        CoinList.map((item) => {
            if (item.category == "soles") {
                item.compra = (compra + venta) / 2
                item.venta = venta
                _coinList.push(item)
            }else if (item.category == "bitcoin") {
                item.compra = btc
                item.venta = btc
                _coinList.push(item)
            } else {
                item.compra = 1
                item.venta = 1
                _coinList.push(item)
            }
        })
        setCoinList(_coinList)
    }
    return {
        coinList, setPrices
    }
}