import axios from "axios"
import errorManager from "./errorManager/errorManager"
//config authorization token 
const token = localStorage.getItem('loginToken')
axios.defaults.headers = {'authorization': `Bearer ${token}`}

const req = {
    get:async (url)=>{
        try {
            const res = await axios.get(url)
            return res.data
        } catch (error) {
            alert("ERROR: ha ocurrido un error inesperado en la peticion get")
            errorManager(error)
            return "error"
        }
    },
    put:async (url)=>{
        try {
            const res = await axios.put(url)
            return res.data
        } catch (error) {
            errorManager(error)
            alert("ERROR: ha ocurrido un error inesperado en la peticion put")
            return "error"
        }
    },
    post:async (url,body)=>{
        try {
            const res = await axios.post(url,body)
            return res.data
        } catch (error) {
            errorManager(error)
            alert("ERROR: ha ocurrido un error inesperado en la peticion post")
            return "error"
        }
    }
}
export default req