import { useEffect, useState } from "react"
import axios from 'axios'
import { DataContext } from '../context/DataContext'
import { useContext } from 'react'
import { apiUrl } from "../services/apiService"
import req from "../services/req"
export const useBoard = () => {

    const { wallet, connect, user, loading, setLoading,setLoadingText } = useContext(DataContext)

    const [addData, setAddData] = useState(false)
    const [ menu, setMenu ] = useState(1)

    const [name,setName] = useState("")
    const [email,setEmail] = useState("")
    const [phone,setPhone] = useState("")

    useEffect(()=>{
        if(user){
            setUserData()
        }
    },[user])

    const setUserData = ()=>{
        if(user){
            setName(user.name)
            setEmail(user.email)
            setPhone(user.phone)
        } 
    }

    const sendFormData = async (e) => {
        setAddData(false)
        setLoading(true)
        setLoadingText("Enviando Dataos de usuario")
        e.preventDefault()
        let body = { _id: user._id, wallet, name, email, phone }
        await axios.put(apiUrl + "user/", body).then(res => console.log(res)).catch((error)=>console.log(error))
        await connect()
        setLoading(false);setLoadingText(false);
    }

    return {
        setName, addData,
        setAddData, 
        setEmail,
        setPhone, sendFormData,
        menu, setMenu,user,loading,name, email, phone
    }
}