import bbva from '../../img/bancos/bbva.png'
import bcp from '../../img/bancos/bcp.png'
import interbank from '../../img/bancos/interbank.png'
import scotiabank from '../../img/bancos/scotiabank.png'
const LogoBank = ({name}) => <img src={
            name === "BCP" ? bcp :
            name === "BBVA" ? bbva :
            name === "Scotiabank" ? scotiabank :
            name === "Interbank" && interbank} alt="" />
export default LogoBank