import { useState, useContext, useRef, useEffect  } from 'react'
import  socket from '../socket'
import { DataContext } from '../context/DataContext'

export const useChat = (idTransaction) => {

    const { user } = useContext(DataContext)
    const down = useRef()
    const [msg, setMsg ] = useState('')
    const [ dataMsg, setDataMsg ] = useState([]);

    const startSocket = () => {
        socket.on('chat', async chat => {

            if(chat.filter( c => c.idUser._id === user._id).length > 0){
                setDataMsg(chat);
                if(down.current){
                    down.current.scrollIntoView()
                }
            }
                
        });
    }

    const getChat = () => {
        fetch(`${process.env.REACT_APP_APIURL}chat/${idTransaction}`);
    }

    const handleMsg = () => {
        if(msg === '') return;
        fetch(`${process.env.REACT_APP_APIURL}chat`,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                idTransaction: idTransaction,
                description: msg,
                idUser: user._id
            })
        })
        .catch( err=> console.log(err))
        setMsg('');
    }

    return {
        dataMsg, down, msg, setMsg, handleMsg, startSocket, getChat
    }
}