import { useEffect, useState } from 'react'
import { motion } from 'framer-motion/dist/framer-motion'
import './detailsTransaction.css'
import LogoBank from '../logoBank/logoBank'
import { useChat } from '../../hooks/useChat.jsx'

const DetailsTransaction = ({ setShowDetails, transactionSelected, dataStatus, handleShowTransaction }) => {

    const { _id, id, moneySend, amountSend, moneyReceive, amountReceive, date, network, bank, bankAdmin, status, payMethod } = transactionSelected

    const { dataMsg, down, msg, setMsg, handleMsg, startSocket, getChat } = useChat(_id)

    const [content, setContent] = useState({ operation: true, chat: false });

    const setTime = (date) => {
        let fecha = new Date(date)
        let time = `${String(fecha.getFullYear())}-${String(fecha.getMonth() + 1).padStart(2, '0')}-${String(fecha.getDate()).padStart(2, '0')}`;
        let hour = `${String(fecha.getHours()).padStart(2, '0')} : ${String(fecha.getMinutes()).padStart(2, '0')}`;
        return { time, hour }
    }

    const sliceFunction = (val) => {
        if (val) {
            return val.slice(-6)
        } else {
            return 0
        }
    }

    useEffect(() => getChat(), []);
    startSocket();

    const styleContentHidden = {
        maxHeight: !content.operation && '75px',
        padding: !content.operation && '25px 0px 55px 35px'
    }

    const styleContetDisplay = {
        display: !content.operation && 'none',
    }

    const styleChatHidden = {
        display: !content.chat && 'none',

    }

    const styleChatDisplay = {
        maxHeight: !content.chat && '75px'
    }

    const styleStatus = {
        opacity: status !== 3 ? .6 : 1
    }

    return (<>
        <motion.div
            className="details-container"
            initial={{ opacity: 0, x: 200, y: -100, scale: .7, position: 'relative' }}
            animate={{ opacity: 1, x: 0, y: 0, scale: 1 }}
            transition={{ type: 'spring', duration: .3 }}
        >
            <div
                className="details-card" style={styleContentHidden} >
                <div
                    className={!content.operation ? 'arrow-content' : 'arrow-content-open'}
                    onClick={() => setContent({ ...content, operation: !content.operation })}>
                    {!content.operation ? (
                        <i className="bi bi-box-arrow-down-left"></i>
                    ) :
                        (
                            <i className="bi bi-box-arrow-up-left"></i>
                        )}
                </div>
                <div className='details-item-transaction'><span className="item-title">N° Transacción</span> <span>#{sliceFunction(_id)}</span></div>
                <div style={styleContetDisplay} className='details-item-hash'><span className="item-title">Hash/Id</span> <span>{id}</span></div>
                <div style={styleContetDisplay} className='details-item-send'><span className="item-title">Enviaste</span> <span>{amountSend} {moneySend}</span></div>
                <div style={styleContetDisplay} className='details-item-recive'><span className="item-title">Recibes</span> <span>{amountReceive} {moneyReceive}</span></div>
                <div style={styleContetDisplay} className='details-item-date'><span className="item-title">Fecha</span> <span>{setTime(date).time} | {setTime(date).hour}</span></div>
                <div style={styleContetDisplay} className='details-item-network'><span className="item-title">Red</span> <span>{network}</span></div>
                <div style={styleContetDisplay} className='details-item-bank card-bank-container'>
                    <div style={styleContetDisplay} className='bank-card'>

                        <span className="item-title">Recibes</span>
                        {moneyReceive == "SOLES" | moneyReceive == "USD" ? <>
                            {moneyReceive} - {bank ? <>
                                Transferencia<br />
                                <div className='cardBank'>
                                    <div className='card-img'>
                                        <LogoBank name={bank.name} />
                                    </div>
                                    <div className='card-title'>{bank.name}</div>
                                    <div className='card-content'>
                                        <span>{bank.titular}</span>
                                        <span>{bank.number}</span>
                                    </div>
                                    <div className='card-footer'>
                                        <span>{bank.type}</span>
                                        <span>{bank.money}</span>
                                    </div>
                                </div>
                            </> : "Efectivo por taquilla"}
                        </> : <div className='suspension'>
                            {moneyReceive}
                            {payMethod.red && <> {payMethod.red} <br /></>}
                            Desde {payMethod && payMethod.method == 1 ? <> Binance Pay </> : <> wallet externa</>}<br />
                            {payMethod && payMethod.pay} <br />
                        </div>}
                        <br/>

                        {/*{moneyReceive == "SOLES" | moneyReceive == "USD" ? <>
                            <span className="item-title">Recibes</span>
                            {bank ? (
                                <div className='cardBank'>
                                    <div className='card-img'>
                                        <LogoBank name={bank.name} />
                                    </div>
                                    <div className='card-title'>{bank.name}</div>
                                    <div className='card-content'>
                                        <span>{bank.titular}</span>
                                        <span>{bank.number}</span>
                                    </div>
                                    <div className='card-footer'>
                                        <span>{bank.type}</span>
                                        <span>{bank.money}</span>
                                    </div>
                                </div>
                            ) : <p>Efectivo</p>}
                        </> : moneyReceive && <>
                            
                            <> Recibe {moneyReceive} </><br />
                            {payMethod.red && <>red: {payMethod.red} <br /></>}
                            A Travez: {payMethod && payMethod.method == 1 ? <> Binance Pay </> : <> Wallet Externa</>}<br />
                            Direccion: {payMethod && payMethod.pay} <br />
                        </>
                        }
 */}
                    </div>
                    <div style={styleContetDisplay} className='bank-card'>
                        {moneySend == "SOLES" | moneySend == "USD" ? <>
                            <span className="item-title">Banco Donde Enviaste</span>
                            {bankAdmin && (
                                <div className='cardBank'>
                                    <div className='card-img'>
                                        <LogoBank name={bankAdmin.name} />
                                    </div>
                                    <div className='card-title'>{bankAdmin.name}</div>
                                    <div className='card-content'>
                                        <span>{bankAdmin.titular}</span>
                                        <span>{bankAdmin.number}</span>
                                    </div>
                                    <div className='card-footer'>
                                        <span>{bankAdmin.type}</span>
                                        <span>{bankAdmin.money}</span>
                                    </div>
                                </div>
                            )}
                        </> :
                            moneySend && <>
                                <span className="item-title mt-4">Metodo de envio</span>
                                <> {moneySend} {payMethod && payMethod.method == 1 ? "Binance Pay" : "Wallet Externa"} </><br />
                                {payMethod.red && <>
                                    Red: {payMethod.red} <br /></>}
                            </>}
                    </div>
                </div>
                <div style={styleContetDisplay} className='details-item-status'><span className="item-title">Estado</span> <span>{dataStatus[status].msg}</span><span className={`transaction-icon ${dataStatus[status].class}`}>{dataStatus[status].icon}</span></div>
            </div>

            <div className='details-card-chat' style={styleChatDisplay}>
                <div
                    onClick={() => setContent({ ...content, chat: !content.chat })}
                    className={!content.chat ? 'arrow-content' : 'arrow-content-open'}>
                    {!content.chat ? (
                        <i className="bi bi-box-arrow-down-left"></i>
                    ) :
                        (
                            <i className="bi bi-box-arrow-up-left"></i>
                        )}
                </div>
                <div className='chat'>
                    <div className='chat-title'>
                        <span className='chat-online'></span>
                        <span>Online</span>
                    </div>
                    <div
                        style={styleChatHidden}
                        className={dataMsg.length > 0 ? 'chat-active' : 'chat-content'}>
                        {dataMsg.length <= 0 ? (<>
                            <div className='chat-icon'><i className="bi bi-chat-left-text-fill"></i></div>
                            <p>Estamos disponible para cualquier consulta</p>
                        </>)
                            : (<>

                                {(dataMsg.map(m => (
                                    // m.idTransaction === transactionSelected._id && (
                                    <div key={m._id} className={m.idUser.level === 0 ? "chat-content-item" : "chat-content-item-admin"} ref={down}>
                                        <span className="chat-content-time">{setTime(m.date).hour}</span>
                                        <p className={m.idUser.level === 0 ? "chat-content-p" : "chat-content-admin"}>{m.description}</p>
                                    </div>
                                    // )

                                )))}
                            </>)
                        }

                    </div>
                    <div
                        className='chat-from'
                        style={{ ...styleStatus, ...styleChatHidden }}>
                        <input
                            onKeyDown={e => e.key === "Enter" && handleMsg()}
                            type='text'
                            name='msg'
                            value={msg}
                            placeholder='Escribe un mensaje...'
                            onChange={e => setMsg(e.target.value)}
                            autoComplete="off"
                            disabled={status !== 3}
                        />
                        <button
                            onClick={() => handleMsg()}
                            disabled={status !== 3}
                        >Enviar</button>
                    </div>
                </div>
            </div>
        </motion.div>
    </>)
}

export default DetailsTransaction;