import LogoBank from '../logoBank/logoBank'
import { useState } from 'react'
import req from '../../services/req'
import { apiUrl } from '../../services/apiService'
import { DataContext } from '../../context/DataContext'
import { useContext } from 'react'
const TransactionModal = ({ setAlertModal,_getChainId, banks, setLoading, input1, input2, setTransactionModal, selected, selected2, wallet, banksAdmin, setInput1, setInput2,setLoadingText }) => {

    const {validateReq} = useContext(DataContext)
    const [selectedBank, setSelectedBank] = useState(false)
    const [code, setCode] = useState(false)
    const [userBank, setUserBank] = useState(false)
    const [pay, setPay] = useState(false)
    const [method, setMethod] = useState(false)
    const [red,setRed] = useState("BTC")


    const changeBank = (value) => {
        const filteredBak = banksAdmin.filter((item) => item._id == value)
        setSelectedBank(filteredBak[0])
    }

    const sendTransaction = async () => {
        let chain = await _getChainId()
        chain == 1 ? chain = "ETH" : chain == 56 ? chain = "BSC" : chain = "MATIC"
        let body = {
            wallet,
            "id": code,
            "bank": "000000000000000000000000",
            "bankAdmin": selectedBank._id,
            "amountSend": input1,
            "amountReceive": input2,
            "moneySend": selected.name,
            "moneyReceive": selected2.name,
            "network": chain,
            "status": 3,
            "payMethod": {
                red,
                method,
                pay
            }
        }
        console.log(body)
        setTransactionModal(false)
        setLoading(true)
        setLoadingText("Enviando Transaccion")
        req.post(apiUrl + "transactions", body).then(_ => {
            console.log(_.response)
            validateReq(_,"sendTransaction/transactionModal.jsx")
            setAlertModal({
                status: true,
                text: "Dirijete a Dashboard / Transacciones para comunicarte con un operador", 
                type: "success", 
                handleBTN: () => window.location = '/#/dashboard'
            })
            setLoading(false);setLoadingText(false);
            setInput1("")
            setInput2("")
        })
    }

    const getRed = ()=>{
        if(selected2.name == "BTC"){
            setRed("BTC")
        }else if(selected2.name == "USDT"){
            setRed("BSC")
        }
    }


    return (<div className="modal-wrapper padModal">

        <div className="modal-display">
            <div className="modalHeader">
                <div className='coinChanges'>
                    {selected.name} <i className="bi bi-arrow-right"></i> {selected2.name}
                </div>
                <div>
                    <i onClick={() => setTransactionModal(false)} className="bi-x x" />
                </div>
            </div>
            <div className="modalContent p-3">
                <span>Elija el banco a donde va a transferir {selected.name} </span>

                <select onChange={e => changeBank(e.target.value)} className="form-control">
                    <option value={false}> - Elija un banco - </option>
                    {banksAdmin && banksAdmin.map((item, index) => {
                        return (item.status == 0 && selected.category == item.money &&
                            <option key={index} value={item._id}>
                                {item.name} - {item.money}
                            </option>
                        )
                    })}
                </select>
                {userBank && <span> Elija su metodo de pago </span>}
                {selected2.style == "fiat" && <>
                    <span>Elije el banco a donde te transferimos {selected2.name} </span>
                    {banks.length == 0 && <div className='textError'>
                        Debe dirigirse a su Dashboard y agregar un banco<br />
                        donde podamos transferir sus {selected2.name} </div>}
                    <select onChange={e => setUserBank(e.target.value)} className="form-control">
                        <option value="0"> - Elija un banco - </option>
                        {banks && banks.map((item, index) => {
                            return (item.status == 0 && selected2.category == item.money &&
                                <option key={index} value={item._id}>
                                    {item.name} - {item.money} - {item.titular}
                                </option>
                            )
                        })}
                    </select>
                </>}
                <div>
                    {selectedBank && <>
                        <select onChange={e => {setMethod(e.target.value);getRed()}} className='form-control mb-2' name="" id="">
                            <option value="false">- Elija un metodo de pago -</option>
                            <option value="1"> Binance Pay </option>
                            <option value="2"> Wallet Externa</option>
                        </select>
                    </>}
                    {method == 2 && selected2.name == "BTC" &&
                    <div className='alertMessage'>
                        <b>IMPORTANTE!</b> Al enviar BTC a una wallet externa se <br></br> 
                        descontara una comision de <b> 0.0005 BTC </b>
                    </div>
                    }

                    {method == "1" && <> <span>Ingrese su correo de Binance Pay</span><br /></>}
                    {method == "1" && <input  onChange={e => setPay(e.target.value)} type="text" className='form-control' placeholder='Correo de Binance Pay' />}
                    {/* {method == "2" && <>
                        <select onChange={e => setRed(e.target.value)} className='form-control mb-2' name="" id="">
                            <option value=""> - Elija la Red - </option>
                            <option value="bsc"> BSC Binance </option>
                            <option value="eth"> Etehereum </option>
                            <option value="matic"> Matic </option>
                        </select>

                    </>} */}
                    {method == 2 && <span>Ingrese su wallet</span>}
                    <br></br>
                    {method == 1 || method == 2 &&
                        <input onChange={e => setPay(e.target.value)} type="text" className='form-control' />
                    }
                </div>

                {selectedBank && method && <>
                    debe transferir {input1} {selected.name} a este numero de cuenta
                </>}
                {selectedBank && method &&
                    <div className='mt-1'>
                        <div className="bList">
                            <div className="bListIn">
                                <LogoBank name={selectedBank.name} />
                                <div>
                                    <h4>{selectedBank.name}</h4>
                                    <b> {selectedBank.number} <br />
                                        {selectedBank.titular}</b>
                                    <h3>{selectedBank.money}</h3>
                                </div>
                            </div>
                        </div>
                    </div>
                }
                {selectedBank && method && <>
                    <div>
                        <p>Numero de transferencia</p>
                        <input onChange={(e) => setCode(e.target.value)} type="text" className="form-control" placeholder="Ingrese su codigo aqui" />
                    </div>

                    <div>
                        <button onClick={sendTransaction}> Enviar </button>
                    </div>
                </>}
            </div>
        </div>
    </div>
    )
}
export default TransactionModal