import { useState } from "react"

const ModalPayType = ({ input1, input2, selected2, selected, setModalPayType, bank, sendMetamask, sendTransactionBinancePay }) => {

    const [method, setMethod] = useState(false)
    const [code, setCode] = useState(false)

    const fixed = (val)=>{
        return Number(val).toFixed(2)
    }

    return <div className='bgtransparentDark'>
        <div className='modal-display'>
            <div className='modalHeader'>
                <div className="b-tittle">
                    Metodo de Pago
                </div>
                <i onClick={() => setModalPayType(false)} className="bi-x x" />
            </div>
            <div className="modalContent">
                <div className="px-4 pt-3">
                    <select onChange={(e) => setMethod(e.target.value)} className="form-select" name="" id="">
                        <option value={false}> - Elija su metodo de pago - </option>
                        <option value="2">Metamask</option>
                        <option value="1">Binance Pay</option>
                    </select>
                </div>
                <div className="px-4">
                    {method && method == "2" ? <div className="metamaskSelectText">
                        <div className="b-result text-center pt-3"> Se cargará a su Metamask </div>
                        <h1 className="text-warning text-center">{fixed(input1)} {selected.name}</h1>
                        <hr />
                        <b className="b-tittle"> Recibe </b> <b className="b-result"> {input2} {selected2.name}</b><br />
                        {bank.name && <><b className="b-tittle"> Banco </b> <b className="b-result"> {bank.name} </b></>}
                        <button onClick={() => { sendMetamask(bank, 2); setModalPayType(false) }} className="btn btn-primary form-control mt-3"> Enviar </button>
                    </div> : method == "1" && <>
                        <div className="send-text w-100">
                            <b> Debe enviar </b>
                            <h4> {fixed(input1)} {selected.name}</h4>
                        </div>
                        <hr />
                        <b className="b-tittle">Correo de binance Pay</b>
                        <h4>manuelperez.0000@gmail.com</h4>
                        <b className="b-tittle"> Recibe </b> <b className="b-result"> {input2} {selected2.name}</b><br />
                        {bank.name ? <><b className="b-tittle"> Banco </b> <b className="b-result"> {bank.name} </b></> : <b className="b-result">Efectivo por taquilla</b>}
                        <hr/>
                        <b className="b-result">Ingrese el id de la transaccion</b>
                        <input onChange={(e) => setCode(e.target.value)} type="text" className="form-control" />
                        
                        <button onClick={() => { sendTransactionBinancePay(code, bank._id || "000000000000000000000000", 1); setModalPayType(false) }} className="btn btn-primary mb-4 mt-3"> Ya he enviado los {selected.name} </button>
                    </>}
                </div>

            </div>
        </div>
    </div>
}

export default ModalPayType