import { useState, useContext } from 'react'
import Bank from '../../components/bank/bank'
import MenuRendererMobile from '../../components/menu/menuMobile'
import MenuRenderer from '../../components/menu/menu'
import dashboardMenu from '../../pages/dashboard/dashboardMenu.json'
import Perfil from './perfil'
import TransactionsDashboard from '../../components/dashboard/transactionDashboard'
import { DataContext } from "../../context/DataContext"
import red from '../../services/selectorDeRed'
import DarkMode from '../../components/darkMode/DarkMode'
import logo from '../../img/logoAzul.png'
import bsc from '../../img/redes/bsc.png'
import matic from '../../img/redes/matic.png'
import eth from '../../img/redes/eth.svg'
import { getStorage, setStorage } from '../../services/localStorage'
import Resume from '../../services/resumeWallet'
const Dashboard = () => {

    const [menu, setMenu] = useState(3)
    const { darkMode, getChainId, wallet, redesModal, setRedesModal, setDarkMode, chain } = useContext(DataContext)

    const handleDarlk = () => {
        let local = getStorage('bitmarketperu')
        if (local === 'light') {
            setStorage('bitmarketperu', 'dark')
            setDarkMode(true)
        } else {
            setStorage('bitmarketperu', 'light')
            setDarkMode(false)
        }
    }

    const dataStyle = {
        background: !darkMode
            && 'linear-gradient(311deg, rgba(118,197,255,1) 0%, rgba(208,235,255,1) 50%, rgba(175,221,255,1) 100%)',
    }

    return (<>
        <div className='d-lg-none nav-section-1 nav-mobile'>
                <div className='logo-mobile'>
                    <img src={logo} alt="" />
                    <span className={`${!darkMode && "nav-text-logo"}`}>BitmarketPerú</span>
                </div>
                <DarkMode setDarkMode={() => handleDarlk()} darkMode={darkMode} />
                <div className='nav-section-2'>
                    <button onClick={() => setRedesModal(true)} className='connect-btn'>
                        <div className='external-circle'>
                            {chain && chain == 1 && <div className='internal-circle'>
                                <img className='redLogoWallet' src={eth} alt="" />
                            </div>}
                            {chain && chain == 56 && <div className='internal-circle'>
                                <img className='redLogoWallet' src={bsc} alt="" />
                            </div>}
                            {chain && chain == 137 && <div className='internal-circle'>
                                <img className='redLogoWallet' src={matic} alt="" />
                            </div>}
                            {!chain && <>
                                <div className='internal-circle'>
                                    <i className='bi-wallet2 text-white' ></i>
                                </div>
                            </>}
                        </div>
                        <div style={{ fontSize: '16px' }}>
                            {wallet ? Resume(wallet) : <>Conectar Wallet</>}
                        </div>
                    </button>
                </div>
            </div>
        <div className='container-dashboard' style={dataStyle}>
            <div className="container-xxl">
                <div className='mobileMargin'>
                    <div className="row">
                        <div className="col-2 sideMenu d-none d-sm-none d-lg-block">
                            <div className={darkMode ? 'dashHeader' : "dashHeaderLight"}>
                                <h3> Dashboard </h3>
                                <p> Bienvenido </p>
                            </div>
                            <ul>
                                <MenuRenderer valueMenu={menu} setMenu={setMenu} menuConfig={dashboardMenu} darkMode={darkMode} />
                            </ul>
                        </div>
                        <div style={{ paddingLeft: '0px', paddingRight: '0px' }} className="col-12 d-block d-sm-block d-lg-none">
                            <div className='sideMenuMobile'>
                                <MenuRendererMobile valueMenu={menu} setMenu={setMenu} menuConfig={dashboardMenu} darkMode={darkMode} />
                            </div>
                        </div>
                        <div className="col-lg-10 col-md-12 text-white">
                            {menu === 3 && <TransactionsDashboard />}
                            {menu === 1 && <Perfil />}
                            {menu === 2 && <Bank />}
                        </div>
                    </div>
                </div>
                <div className='mobileSeparator d-lg-none'></div>
            </div>
        </div>

        </>)
}
export default Dashboard